@import '/src/styles/utils/variables';

.nav{
&__list{
  margin: 0;
  padding: 0;
  list-style: none;

  display: flex;
  flex-direction: column;
  gap: 32px;
}

&__link {
  position: relative;
  color: $main-text-color;
  text-decoration: none;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: $letter-spacing;
  display: inline-block;

  &::after{
    content: '';
    position: absolute;
    bottom: -8px;
    left: 0;
    display: block;
    height: 1px;
    width: 100%;
    background-color: $main-text-color;

    transition: transform 0.5s;
    transform: scale(0);
    transform-origin: left;
  }

  &:hover::after {
    transform: scale(1);
  }
}
}

